*{
  margin: 0;
  padding: 0;
}

.home .MlargeLogo{
  width: 100%;
  height: 675px;
  background: #FAF8F8;
}
.titleMenu{
  width: 100%;
  height: 95px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pasek{
  background: #FED94B;
  width: 75px;
  height: 4px;
}
.menu{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 280px;
}
.menu p{
  color: #000;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 25px;
}
.MlargeLogo .przyciski{
  display: flex;
  justify-content: space-evenly;
}
.MlargeLogo .przyciski button{
  width: 215px;
  height: 75px;
  border: 3px solid #D5D3CE;
  border-radius: 20px;
  cursor: pointer;
  font-size: 17px;
  font-weight: 300;
  background: #FFF;
  &:hover{
    transition: 0.5s;
    border: 3px solid #FED94B;
  }
}
.MlargeLogo img{
  padding-top: 30px;
  height: 120px;
}
.starters{
  width: 100%;
  height: 330px;
  display: flex;
  justify-content: center;
}
.startersIn{
  width: 90%;
  height: 100%;
  //background: pink;
  display: flex;
  justify-content: space-between;
}
.biggerS{
  height: 100%;
  width: 55%;
  border: 2px solid #D5D3CE;
  border-radius: 20px;
  display: flex;
}
.smallerS{
  height: 100%;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  //background: yellow;
}
.starter1{
  height: 43%;
  width: 100%;
  border: 2px solid #D5D3CE;
  border-radius: 20px;
}
.starter2{
  height: 43%;
  width: 100%;
  border: 2px solid #D5D3CE;
  border-radius: 20px;
}
.Mmenu{
  height: 2100px;
  background: #FAF8F8;
  img{
    padding-top: 30px;
    height: 130px;
  }
}
.drinkBox1{
  height: 100%;
  width: 48%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.starters .drink{
  height: 43%;
  width: 100%;
  border: 2px solid #D5D3CE;
  border-radius: 20px;
}
.BigStarter{
  height: 100%;
  width: 240px;
  background: pink;
}
.BigStarterInfo{
  background: red;
  height: 100%;
  width: 476px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Shrimps{
  height: 49px;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  background: green;
}
.Lorem{
  height: 81px;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
}
.price{
  padding-top: 40px;
  height: 65px;
  background: yellow;
}


