@import "menu";
@import "more";
*{
  margin: 0;
  padding: 0;
}
//font-family: 'Libre Baskerville', serif;
//font-family: 'Open Sans', sans-serif;
.home{
  width: 100%;
  height: 750px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.57), rgba(0, 0, 0, 0.57)), url("../images/lemon.jpg");
  display: flex;
  flex-direction: column;
  background-position: left;
}

header{
  height: 75px;
  width: 100%;
  background: rgba(0, 0, 0, 0.61);
  //opacity: 0.61;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo{
  display: flex;
}
.logo img{
  width: 95px;
  height: 70px;
  padding-left: 10px;
}
.logo h1{
  color: #FED94B;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 35px;
}
.h1lemonzz{
  display: flex;
  align-items: center;
  padding-left: 5px;
}
.buttons{
  width: 145*5px;
  height: 100%;
  display: flex;
}
.buttons button{
  width: 145px;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  color: white;
  border: none;
}
.buttons button {
  &:hover {
    transition: 0.5s;
    background: rgba(0, 0, 0, 0.3);
  }
  cursor: pointer;
  transition: all 0.5s;
  &:after {
    content: '';
    position: relative;
    display: block;
    opacity: 0;
    bottom: -27px;
    width: 100%;
    height: 4px;
    transition: 0.5s;
    background: #FED94B;
  }
  &:hover:after {
    opacity: 1;
  }
}
.largeLogo{
  height: 675px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.largeLogo h1{
  font-family: 'Libre Baskerville', serif;
  color: #FED94B;
  font-size: 80px;
  font-weight: 300;
  text-align: center;
}
.LemonSlogan{
  color: white;
  font-family: 'Libre Baskerville', serif;
  font-size: 30px;
  text-align: center;
  font-weight: 300;
}
.LemonLogo{
  height: 173px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
}
.about_me{
  width: 100%;
  height: 2600px;
  background-image: linear-gradient(rgba(252, 252, 252, 0.61), rgba(252, 252, 252, 0.61)), url("../images/growing-meyer-lemon-trees-in-pots-848166-02-0e949f5ad3c845499964e908131b2f66.jpg");
}
